.TileContainer {
  position: absolute;
  top: 23rem;
  left: 50%;
  flex-shrink: 0;
  font-family: 'Calistoga', cursive;
  font-weight: 700;
  font-size: 3.5rem;
  padding: 3rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url(../assets/frame-2.png);
  background-size: cover;
  box-shadow: inset 0 0 10px 4px rgba(0, 0, 0, 0.507);
  border-radius: 7px;
  width: 45rem;
  height: 45rem;
  backface-visibility: hidden;
  transform: scale(0.6) translate(-50%, 0) rotate(15deg); }
  .TileContainer_box {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(../assets/box.jpg);
    box-shadow: inset 0 0 6px 4px rgba(0, 0, 0, 0.5);
    background-repeat: no-repeat;
    background-size: cover;
    display: grid;
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 0.3rem;
    padding: 0.5rem;
    border-radius: 5px;
    backface-visibility: hidden;
    transform: translateZ(0) scale(1, 1); }

